.menu-icon {
  position: relative;
  top: 10px;
  display: none;
  width: 3em;
  height: 5em;
  cursor: pointer;
  margin: 0 auto;
  float: none;
  -webkit-transition: all 0.3s;
  transition: all 0.3s; }

.menu-icon > span {
  position: absolute;
  top: 50%;
  display: block;
  width: 100%;
  height: 0.3em;
  margin-top: -0.5em;
  background-color: #fff;
  border-radius: 3px; }

.menu-icon > span:before,
.menu-icon > span:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: 3px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s; }

.menu-icon > span:before {
  -webkit-transform: translateY(-1em);
  transform: translateY(-1em); }

.menu-icon > span:after {
  -webkit-transform: translateY(1em);
  transform: translateY(1em); }

/* OPENED */
.menu-icon.open {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg); }

.menu-icon.open > span:before {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg); }

.menu-icon.open > span:after {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg); }

